import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { DashboardService } from 'src/app/shared/services/dashboard.service';
import {test} from 'src/app/shared/utilities/dashboard-util'

 

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
})
export class HeaderComponent implements OnInit {

  constructor(private dashboardService : DashboardService,private route: ActivatedRoute,private modalService: NgbModal) { }
  public result;
  public newRes;
  public color="green";
  public param;
  public popupModal
  @ViewChild('addNum') addNum;
  public modal_options: NgbModalOptions = {
    size: 'lg',
    backdrop: 'static'
  };
  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.param = this.route.snapshot.params['number']
      console.log(this.param);
    });
    console.log(this.dashboardService.mainResult)
    this.display();
  }
  ngAfterViewInit() {
    setTimeout(() => this.openPopup());
  }
  openPopup(){
    this.popupModal = this.modalService.open(this.addNum,this.modal_options);
  }
  display(){
    this.dashboardService.mainResult = 6;
    console.log(this.dashboardService.mainResult)
    this.result = test(2,5); 
    this.newRes = this.dashboardService.displayVal(6,7);
  }

}
